<template>
	<div class="hot">
		<div class="hot-Introduce">
			<div class="hot-Introduce-title">
				<div class="hotTitle">
					<a>全部分类</a>
				</div>
				<span v-for="item in alllistTags">
					<div class="box">
						<a class="hotTitle1" @click="findalltoplayList(item.name)">{{item.name}}</a>
					</div>
				</span>
			</div>
		</div>
		<!-- 热门推荐内容 -->
		<div class="hot-main">
			<div class="intTitle">
				<a>推荐歌单</a>
			</div>
			<ul class="hot-main-content" v-for="item in alltoplayList">
				<div id="box">
					<img class="hot-main-pics" :src="item.coverImgUrl" alt="" @click="findList(item.id)">
				</div>
				<p class="hot-main-list" @click="findList(item.id)"> {{item.name}}</p>
				<p class="hot-main-list1"> {{item.copywriter}}</p>
				<p class="hot-main-list2"> {{item.creator.nickname}}</p>
			</ul>
		</div>
	</div>
</template>

<script>
	import store from '../../store/index.js'
	export default {
		data() {
			return {
				/* 推荐歌单 */
				alltoplayList: [],
				/* 获得热门歌单类别 */
				alllistTags: [],
				
			}
		},
		methods: {
			/* 所有歌单分类 */
			allplaylistTags() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/highquality/tags")
					.then((response) => {
						that.alllistTags = response.data.tags;
					}, (err) => {})
			},
			/* 发现热门推荐歌单 */
			findalltoplayList(cat) {
				var that = this;
				this.$axios.get("https://autumnfish.cn/top/playlist/highquality&cat="+cat)
					.then((response) => {
						that.alltoplayList = response.data.playlists;
					}, (err) => {})
			},
			
			findList(id) {
				this.$store.commit("findId", id);
				this.$router.push("playListDetail");
			},
		},
		mounted: function() {
			this.allplaylistTags();
			this.findalltoplayList("全部");
		}
	}
</script>

<style scoped>
	.hot {
		padding-top: 50px;
		font-size: 18px;
		width: 100%;
		height: 2300px;
		background-color: #f5f5f5;

	}

	.hotTitle {
		margin-left: 115px;
	}

	.hotTitle,
	.intTitle {
		font-size: 25px;
		width: 500px;
		height: 50px;
		font-weight: 700;
		color: #323232;
	}
	
	.intTitle{
		margin-left: 50px;
	}
	.hot-Introduce-title {
		width: 100%;
		height: 300px;
	}

	.hot-Introduce-title .box {
		float: left;
		width: 200px;
		color: #8b8b8b;
	}

	.box {
		text-align: center;
		margin-top: 10px;
	}

	a {
		margin: 10px 10px;
	}

	.hotTitle1 {
		margin-left: 100px;
	}

	.hot-main {
		width: 90%;
		padding-left: 55px;
	}

	.hot-main .hot-main-content {
		float: left;
		width: 180px;
		height: 370px;
	}

	.hot-main-pics {
		width: 190px;
		height: 190px;
		border: 1px solid #d3d3d3;
		border-radius: 10px;
		box-shadow: 0 2px 4px rgba(0, 0, 0, .788), 0 0 6px rgba(0, 0, 0, .04);
	}

	.hot-main-list1,
	.hot-main-list2 {
		color: #8b8b8b;
		font-size: 17px;
	}

	#box {
		width: 200px;
		height: 200px;
		border: red;
		margin: 10% auto;
		overflow: hidden;
		border-radius: 10px;
	}

	img {
		transform: all 5s;
	}

	img:hover {
		transform: scale(2);
	}
	.hotTitle1:hover{
		color: #C20C20;
		cursor: pointer;
	}
	.hot-main-pics,.hot-main-list:hover{
		color: #C20C20;
		cursor: pointer;
	}
</style>
